/* ==========================================================================
   Twitter widget style - begin
   ========================================================================== */

.widget.widget_qodef_twitter_widget {
    margin: 0 0 20px;
    
    .qodef-twitter-widget {
        @include qodefRelativeHolderLayout();
        margin: 0;
        
        li {
            @include qodefRelativeHolderLayout();
            
            &:not(:last-child) {
                margin: 0 0 22px;
            }
            
            .qodef-tweet-text {
                position: relative;
                
                a {
                    position: relative;
                    line-height: inherit;
                    color: inherit;
                    
                    &.qodef-tweet-time {
                        display: block;
                        
                        span {
                            margin: 0 2px 0 0;
                        }
                    }
                }
            }
        }
        
        &.qodef-twitter-standard {
            
            li {

                padding: 30px;

                color: #fff;
                margin: 0;
                box-sizing: border-box;
                background-color: $first-main-color;
                background-image: url('../img/twitter-background.png');
                background-position: right bottom;
                background-repeat: no-repeat;

                .qodef-twitter-autor {
                    position: relative;
                    color: #fff;
                    padding-left: 35px;
                    border-bottom: 1px solid rgba(#fff, 0.2);
                    margin-bottom: 22px;

                    .qodef-twitter-autor-title {
                        margin: 0;
                        color: #fff;
                        padding-bottom: 12px;
                    }

                    .qodef-twitter-icon {
                        position: absolute;
                        left: 0;
                        color: #fff;

                        i {
                            font-size: 20px;
                        }
                    }
                }
                
                .qodef-tweet-text {

                    a,
					span {
                        color: #fff;
                    }
                    
                    a {

                        font-size: 12px;
                        display: block;
                        margin:5px 0;
                        
                        &:hover {
                            opacity: 0.8;
                        }
                    }
                }

                .qodef-tweet-time {
                    margin-top: 22px;
                }
            }
        }

        &.qodef-twitter-slider {
            @include qodefRelativeHolderLayout();
            padding: 0 40px;
            text-align: center;
            box-sizing: border-box;
            
            li {
                overflow: hidden;
                
                .qodef-twitter-icon {
                    
                    i {
                        font-size: 16px;
                        color: $first-main-color;
                    }
                }
                
                .qodef-tweet-text {
                    font-size: 14px;
                    
                    a,
                    span {
                        color: $first-main-color;
                    }
                    
                    a {
                        
                        &:hover {
                            color: $default-heading-color;
                        }
                        
                        &.qodef-tweet-time {
                            margin: 21px 0 0;
                        }
                    }
                }
            }
            
            .owl-nav {
                
                .owl-prev {
                    left: 0;
                }
                
                .owl-next {
                    right: 0;
                }
            }
        }
    }
}
/* ==========================================================================
   Twitter widget style - end
   ========================================================================== */