// widgets spacing
$widget-bottom-margin: 19px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;

// widget color
$widget-background-color: transparent;
$widget-border-color: transparent;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 0px;

// list items color
$widget-list-item-background-color: #ffffff;
$widget-list-item-border-color: transparent;

// list items border
$widget-list-item-border-color: transparent;
$widget-list-item-border-color-second: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// title
$widget-title-color: $default-heading-color;

// text
$widget-text-color: inherit;
$widget-heading-color: inherit;

// links
$widget-link-color: $default-heading-color;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;
$blog-list-padding-left: 20px;

aside.qodef-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;
        background-color: $widget-background-color;
        border-color: $widget-border-color;

        &:last-child {
            margin: 0 !important;
        }

        .qodef-widget-title-holder {
            color: $widget-title-color;
            border-bottom: 1px solid $default-border-color;

        }

        ul {
            li {
                a {
                    text-transform: uppercase;
                    font-weight: 500;
                    letter-spacing: 0.1em;
                }
            }

        }

        &.qodef-recent-post-widget {
            .qodef-recent-posts {
                .qodef-rp-item {
                    margin: 0;
                    padding: 27px 15px 27px 0;
                    border-bottom: 1px solid $default-border-color;
                }

                .qodef-rp-title {
                    font-size: 20px;
                    font-weight: 600;
                    font-family: $default-heading-font;
                    line-height: 1em;
                }
            }
        }

        &.widget_categories {

            ul {
                margin-top: 19px;
            }

        }

    }

}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .qodef-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.qodef-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-flat';

    .widget {

        &.widget_archive,
        &.widget_categories {

            ul {

                margin-top: 21px !important;

                li {

                    padding: 4px 0;

                    a {
                        font-size: 12px;
                        font-family: $default-text-font;
                        color: $widget-link-color;
                        font-weight: 400;
                        letter-spacing: 0.15em;
                    }

                }

            }

        }

    }

}