.single-events {
	.qodef-ttevents-single-image-holder {
		margin-bottom: 48px;

		img {
			display: block;
		}
	}

	.tt_event_columns{

		.tt_event_column_left{

			width:45%;
			margin-right: 0px;
			padding-left: 15px;
			padding-right: 15px;
			box-sizing: border-box;
		}
		.tt_event_column_right{

			width:55%;
			padding-left: 15px;
			padding-right: 15px;
			box-sizing: border-box;

			h4{
				font-family: $default-heading-font;
				font-weight	: 600;
				font-size:30px;
				color:$default-heading-color;
				margin-top: 33px;
			}

			p {
				color:$second-text-color;
				font-size: 16px;
				font-family: $default-text-font;
			}

		}
	}


	.qodef-ttevents-single-title {
		margin-top: 0;
		margin-bottom: 14px;
	}

	.qodef-event-single-icon {
		float: left;
		margin-right: 10px;
		color: $first-main-color;
		font-size: 16px;
	}

	.qodef-ttevents-single-subtitle {
		margin: 0 0 17px;
		color: $first-main-color;
	}

	.qodef-ttevents-single-content {
		margin-top: 10px;
	}

	h3.tt_event_margin_top_27 {
		line-height: 35px;
		padding-top: 30px;
		padding-bottom: 5px;
        border-top: 1px solid #e5e5e5;
		margin-top: 36px !important;

        .tt_event_hours_count {
            color: inherit;
            font-weight: inherit;
        }
	}

	.tt_event_hours li {
		border: none;
		background-color: #f5f5f5;
		padding: 39px 30px 38px;;
		margin-bottom: 2.8%;
		width: 48%;
		margin-right: 2.8%;
		font-size: 10px;
		color: $default-text-color;
		letter-spacing: 0.2em;
		text-transform: uppercase;

        border-radius: 5px;
		box-sizing: border-box;

		&:nth-child(2n) {
			margin-right: 0;
		}

		h4 {
			margin: 0;
			text-transform: uppercase;
			font-size: 12px;
            color: $second-text-color;
			font-family: $default-text-font;
			font-weight: 400;

			&:nth-of-type(1) {
				margin-bottom: 11px;
				color: $first-main-color;
				font-size: 12px;
				font-family: $default-text-font;
				font-weight: 400;
				letter-spacing: 0.2em;
			}
		}

		p {
			margin-top: 0;
			font-family: $default-text-font;
			font-weight: 400;
			text-transform: none;
			color: $second-text-color;
			padding-top: 10px;
            font-size: 16px;
            letter-spacing: 0;
		}
	}

	.tt_event_items_list {
		margin: 0;


		li.type_info {
			border-bottom: 1px dashed #cdcdcd;
			padding: 0;
            margin-bottom: 22px;

			p {
				display:inline;
			}

			.tt_event_text {
				font-family: $default-text-font;
				color: $default-text-color;
				font-size: 16px;
				font-weight: 400;
				line-height: 20px;
				text-align: left;
				margin-left: 20px;
			}

			label {
				font-family: $default-heading-font;
				font-size: 20px !important;
				text-transform: uppercase;
				font-weight: 600 !important;
				color: $first-main-color;
                letter-spacing: 0.1em;

				&:after{
					content: ' ';
					width: 3px;
					height: 4px;
					background-color: $first-main-color;
					position: relative;
					display: inline-block;
					margin: 0 0 3px 1px;
				}
			}
		}

		li:not(.type_info) {
			background: none;
			font-family: $default-text-font;
			font-size: 16px;
			color: $second-text-color;
			margin-bottom: 15px;
			padding-left: 27px;
			position: relative;

			&:before {
				content: '\4e';
				position: absolute;
				left: 0;

				font-family: 'ElegantIcons';
				color: $first-main-color;
				font-size: 18px;
			}
		}
	}
}

.tt_event_theme_page{

	h3 {
		font-family: $default-heading-font;
		font-weight	: 600;
		font-size:30px;
		color:$default-heading-color;
	}

	h5 {
		font-family: $default-heading-font;
		font-weight	: 600;
		font-size:30px;
		color:$default-heading-color;
	}

	.tt_event_page_left, .tt_event_page_right{
		padding-left: 25px;
		padding-right: 25px;
		box-sizing: border-box;
	}

	p{
		color:$second-text-color;
	}
}

.tt_event_page_left{
	width:75%;
}

.tt_event_page_right{
	width:25%;
}

.qodef-grid-1200 {

	.tt_event_theme_page {
		width: 1200px;
	}
}

.qodef-grid-1300 {

	.tt_event_theme_page {
		width: 1300px;
	}
}

