@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.qodef-comment-holder {
    @include qodefRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .qodef-comment-holder-inner {
        padding: 48px 0 20px;
    }
	
	.qodef-comment {
		
		&.qodef-pingback-comment {
			
			.qodef-comment-text {
				padding: 0;
			}
		}
	}

    .qodef-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include qodefRelativeHolderLayout();
            margin: -3px 0 0;
            padding: 29px 0 0;

            .qodef-comment {
                padding: 20px 30px 36px;
                background-color: $second-background-color;
                border-radius: 5px;
            }

        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 104px;
        }
    }

    .qodef-comments-title {
        margin-bottom: 28px;
        text-transform: uppercase;
        letter-spacing: 0.2em;
        color:$default-heading-color;

        > * {
            margin: 0;
        }
    }

    .qodef-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 61px;
        height: 61px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .qodef-comment-text {
        padding: 0 0 0 84px;
        min-height: 82px;

        .qodef-comment-info {
            @include qodefRelativeHolderLayout();
            margin: -12px 0 22px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 12px;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            color:$second-text-color;

            &:hover {
                color: $first-main-color;
            }
        }

        .qodef-comment-name {
            margin: 0;
            text-transform: uppercase;
            letter-spacing: 0.1em;
        }

        .qodef-comment-date {
            float: left;
            margin-top: -8px;
            font-size: 12px;
            letter-spacing: 0.1em;
            text-transform: uppercase;
        }


        .qodef-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }

            &#respond {
                textarea,
                input[type="text"] {
                    background-color: #fff;
                }
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.qodef-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .qodef-comment-form-inner {
        padding: 35px 0 60px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .qodef-comment-holder {
	    
        .qodef-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .qodef-comment-holder {
	    
        .qodef-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .qodef-comment-text {
	        
            .qodef-comment-date,
            .qodef-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
                text-transform: uppercase;
                letter-spacing: .2em;
                color:$second-text-color;

            }
        }
    }
}

@include phone-portrait {
	
    .qodef-comment-holder {

        .qodef-comment-image {
            float: none;
        }

        .qodef-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.qodef-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
    border-color:transparent;
    font-size:15px;
    font-family: $default-text-font;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include qodefButtonDefaultStyle();
	@include qodefButtonSmallParamsIcons();
	@include qodefButtonSolidColor();
	cursor: pointer;
    font-family: $default-text-font;
    font-size: 11px;
    padding: 12px 13px;
    z-index: 2;
	
	&:hover {
		@include qodefButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.qodef-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.qodef-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.wpcf7-form-control.wpcf7-text{
    color:#313646;
    font-size: 15px;
    font-family: $default-text-font;
    font-weight: 400;
    border: 1px solid transparent;
}

.wpcf7-form-control.wpcf7-textarea {
    color:#313646;
    font-size: 15px;
    font-family: $default-text-font;
    font-weight: 400;
    border: 1px solid transparent;
}

.wpcf7-form-control.wpcf7-submit{
    font-family: $default-heading-font;
}

.qodef-custom-cf-7-button {
    input {
        position: absolute !important;
        color: transparent !important; /* hide default text for cf-7 */

        left: 0;
        top: 0;
        width: 100% !important;
        height: 100% !important;
        border: none !important;
        background-color: transparent !important;
    }

}

div.wpcf7 .qodef-custom-cf-7-button .ajax-loader {
    right: -50px;
}

div.wpcf7 .ajax-loader {
    background-repeat: no-repeat !important;
    position: absolute !important;
    right: 0;
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.qodef-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.qodef-slider-is-moving {
		
		.owl-item {
			
			.qodef-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.qodef-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include qodefTransform(translateY($pos));
            }

        }
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include qodefTransform(translateY(-50%));
			@include qodefTransition(color .2s ease-out, margin .2s ease-out);
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 60px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}

        @include ipad-landscape {

            .owl-prev,
            .owl-next {
               display:none;
            }

        }
		
		.owl-prev {
			left: 30px;
            &:hover {
                margin-left: -8px;
            }
		}
		
		.owl-next {
			right: 30px;
            &:hover {
                margin-right: -8px;
            }
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				margin: 0 6px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid rgba($default-heading-color, .5);
				box-sizing: border-box;
				@include qodefTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .qodef-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .qodef-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            img {
                border: 2px solid transparent;
                @include qodefTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.qodef-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .qodef-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }

    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .qodef-wrapper {
        padding: 0 !important;
    }
    
    .qodef-content {
	    height: 100vh;
        background-color: #eee;
    }
	
	&.admin-bar {
		
		.qodef-content {
			height: calc(100vh - 32px);
		}
	}

    .qodef-content-inner {
	    @include qodefTableLayout();
    }

    .qodef-page-not-found {
	    @include qodefTableCellLayout();
        padding: 0 10%;
	    text-align: center;

        .qodef-404-title {
            margin: 0;
            font-size: 200px;
            letter-spacing: 35px;
            line-height: 1em;
        }
	
	    .qodef-404-subtitle {
            font-size: 60px;
            margin: 8px 0 0;
            letter-spacing: 0.2em;
        }

	    .qodef-404-text {
		    margin: 10px 0 0;
	    }

        form {
            display: inline-block;
            position: relative;
            width: 850px;
            max-width: 100%;
            margin: 106px 0 auto;

            .search-field {
                width: 100%;
                box-sizing: border-box;
                padding: 10px 40px 10px 20px;
                line-height: 32px;
                border: none;
                background-color: #fff;
                outline-color: transparent;
            }

            .qodef-search-submit {
                position: absolute;
                right: 0;
                top: 0;
                width: 70px;
                box-sizing: border-box;
                padding: 10px 5px;
                line-height: 32px;
                border: none;
                color: $first-main-color;
                background-color: transparent;
                font-size: 30px;
                cursor: pointer;


                @include qodefTransition(color 0.2s ease-out);

                &:hover {
                    color: $default-heading-color;
                }
            }
        }

        .qodef-btn {
            // margin: 30px 0 0;

            &.qodef-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }
	
	@include ipad-landscape {
		
		.qodef-content {
			height: 100%;
			min-height: 100vh;
            margin-top: -69px !important;

            .qodef-content-inner {
                padding-top: 185px;
            }
		}
		
		&.admin-bar {
			
			.qodef-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .qodef-page-not-found {
            padding: 70px 30px 20px;
        }
    }

    @include phone-landscape {

        .qodef-page-not-found {
            .qodef-404-title {
                font-size: 100px;
                letter-spacing: 5px;
            }
            .qodef-404-subtitle {
                font-size: 40px;
                letter-spacing: .1em;

            }
            form {
                margin: 60px 0 auto;
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#qodef-back-to-top {
    position: fixed;
    width: 41px;
    height:41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    overflow:hidden;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include qodefTransition(opacity 0.15s cubic-bezier(0.86, 0.15, 0.19, 0.93), visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include qodefTransition(opacity 0.35s cubic-bezier(0.86, 0.15, 0.19, 0.93));
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 16px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;

        &:before {
            content:"";
            opacity:0;
            z-index:50;
            background-color:#000;
            @include qodefTransition(opacity .35s cubic-bezier(0.86, 0.15, 0.19, 0.93));
            @include qodefAbsoluteHolderLayout();
        }

        i {    
            position:relative;
            display:block;
            z-index:100;
            @include qodefTransitionTransform(.25s cubic-bezier(0.86, 0.15, 0.19, 0.93));
        }

        &:last-of-type {
            @include qodefAbsoluteHolderLayout();

            i {
                @include qodefTransform(translate3d(0%,100%,0%));
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }

    &:hover {
        > span {
            i {
                @include qodefTransform(translate3d(0%,-100%,0%));
            }

            &:last-of-type { 
                i { 
                    @include qodefTransform(translate3d(0%,0%,0%));
                }
            }

            &:before { 
                opacity:0.07;
            }
        }
    }

}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.qodef-self-hosted-video-holder {
	
    .qodef-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}


.qodef-custom-contact-form {
    max-width: 824px;
    margin: auto;

    .wpcf7-form-control.wpcf7-text {
        line-height: 22px;
    }

    > *:first-child {
        width: 70%;
        float: left;
    }

    > *:last-child {
        width: 26%;
        text-align: right;
        float: left;
    }

    @include ipad-landscape {
        > *:first-child {
            width: 68%;
            float: none;
        }
        > *:last-child {
            float: none;
            width: 32%;
            text-align: center;
        }
    }

    @include ipad-portrait {
        > *:first-child {
            width: 100%;
            float: none;
        }
        > *:last-child {
            float: none;
            width: 100%;
            text-align: center;
        }
    }

}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */