// widgets spacing
$widget-bottom-margin: 29px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #484848;
$widget-list-item-border-width: 0;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #fff;

// links
$widget-link-color: #717274;
$widget-link-hover-color: $header-light-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;


footer {
    @import 'widgets/default-wordpress-widgets-flat';

    .wpcf7-form-control.wpcf7-text {
        color: #a4a4a4;
        font-size: 14px;
        font-family: $default-text-font;
        font-weight: 600;
        border: 1px solid transparent;
        background-color: #212121;
        margin: 5px 5px 5px 0;
    }

    .wpcf7-form-control.wpcf7-textarea {
        color: #a4a4a4;
        font-size: 14px;
        font-family: $default-text-font;
        font-weight: 600;
        border: 1px solid transparent;
        background-color: #212121;
        margin: 5px 5px 5px 0;
        min-height: 105px;
    }

    .wpcf7-form-control.wpcf7-submit{
        margin: 5px 5px 5px 0 !important;
    }

    .widget {

        &.widget_archive,
        &.widget_categories {

            ul {
                margin-top: 24px;

                li {
                    margin: 10px 0;
                }
            }
        }

        .qodef-blog-list-holder ul.qodef-blog-list li {
            margin-top:0px !important;
        }

    }

}

