/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.qodef-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }

    }
}


.wpb_widgetised_column {
    position: relative;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
    }
}

/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 35px;

    .qodef-widget-title {
        margin: 0 0 17px;

        a {
            color: inherit;
        }

        .qodef-widget-title {
            margin-bottom: 11px;
        }
    }


    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            margin: 0 0 10px;

            &:last-child {
                margin: 0;
            }
        }
    }

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }

    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .qodef-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include qodefRelativeHolderLayout();
            clear: both;

            > div {
                @include qodefRelativeHolderLayout();
                border-width: 1px;
                border-style: solid;
                border-color: $default-border-color;
                box-sizing: border-box;
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-family: inherit;
            font-weight: 400;
            font-size: 15px;
            line-height: 32px;
            color: $default-text-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include qodefTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 40px);
            padding: 10px 0 10px 21px;

            @include qodefPlaceholder {
                color: inherit;
            }
        }

        button {
            width: 40px;
            padding: 10px 0;
            font-size: 13px;
            color: $default-text-color;
            cursor: pointer;
            @include qodefTransition(color .2s ease-in-out);

            &:hover {
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 4px 0;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_recent_entries {

        .qodef-widget-title {
            margin: 0 0 23px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
                    font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {

            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .qodef-widget-title {
            margin: 0 0 8px;
        }

        .tagcloud {

            padding-top: 22px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                color: $default-text-color;
                font-family: inherit;
                font-weight: 400;
                font-size: 17px !important;
                text-transform: uppercase;
                background-color: transparent;

                &:last-child {

                    &:after {
                        display: none;
                    }
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.qodef-side-menu,
    .qodef-page-footer {

    .widget {
        color: #b1b2b2;

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
            color: #fff;
        }

        .qodef-widget-title {
            color: #fff;
            margin: 0 0 27px;
        }

        a {
            color: #717274;
        }

        table thead tr,
        table tbody tr {
            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
        #wp-calendar {

            caption {
                color: #b1b2b2;
            }
        }

        &.widget_rss {

            .qodef-footer-widget-title {

                .rsswidget {
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: #b1b2b2;
                }
            }

            input {
                color: #b1b2b2;

                @include qodefPlaceholder {
                    color: inherit;
                }

                &[type='submit'] {
                    color: #b1b2b2;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;
                border-color: #b1b2b2;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}

.qodef-top-bar {

    a {
        color: #969696;

        &:hover {
            color: $first-main-color;
        }
    }

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;

            .textwidget{
                p {
                    font-size: 16px;
                    color:#bdbdbd;
                    font-family: $default-text-font;
                }
            }

        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}

/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */