/* ==========================================================================
   Blog List widget style - begin
   ========================================================================== */

.widget.qodef-blog-list-widget {
    margin: 0 0 20px;

    > h4 {
        margin: 0 0 15px;
    }

    footer .qodef-footer-top &,
    .qodef-top-bar & {

        > h2 {
            margin: 0 0 8px;
        }
    }

    .qodef-blog-list-holder.qodef-bl-boxed-boxed-shadow .qodef-bli-inner .qodef-bli-content {
        box-shadow: none;

        .qodef-post-title {
            margin: 12px 0 7px;
        }

        .qodef-post-info-date {

            margin: 0 0 25px;

            a {
                font-family: $default-text-font;
                font-size: 13px;
                color: #6b6b6b;
                font-weight: 500;
            }
        }
    }

    footer & .qodef-blog-list-holder {
        padding-top: 12px;

        &.qodef-bl-minimal .qodef-post-info-date {
            margin: 8px 0 12px;

            a:hover {
                color: #fff;
            }
        }

        ul.qodef-blog-list .qodef-bl-item {
            margin-bottom: 17px;
            .qodef-bli-inner {
                padding-bottom: 19px;
            }

             &:not(:last-child) .qodef-bli-inner {
                border-bottom: 1px solid #484848;
            }
        }
    }

    .qodef-large-space .qodef-outer-space .qodef-item-space {
        margin: 25px 0 25px;
    }

    .qodef-normal-space .qodef-outer-space .qodef-item-space{
        margin: 25px 0 25px;
    }

    .qodef-bl-wrapper {

        ul.qodef-blog-list {

            li {

                .qodef-bli-inner {

                    .qodef-post-image{
                        width: 61px;
                        .qodef-sidebar-holder & {
                            width: 80px;
                        }
                    }

                    padding-bottom: 25px;

                    .qodef-bli-content {

                        vertical-align: middle;

                        .qodef-sidebar-holder & {
                            vertical-align: top;
                        }

                        .qodef-post-info-date {
                            font-weight: 400;
                            margin: 2px 0 0;
                            letter-spacing: 0.15em;
                            a {
                                font-size: 12px;
                            }
                        }
                    }
                }
            }

            & li:not(:last-child) {

                .qodef-bli-inner {

                    border-bottom: 1px solid $default-border-color;

                }

            }

        }

    }
}

/* ==========================================================================
   Blog List widget style - end
   ========================================================================== */